import React from 'react'



const SearchBox = ({searchfield, searchchange}) => {
	return (
				<div>
					<input className = 'pa3 i b helvetica #9eebcf b--dashed br-3'
					 type = 'search' 
					 placeholder = 'Please enter the name of your favorite robot' 
					 onChange = {searchchange}
					 />
				</div>
	);
}

export default SearchBox;















