import React from 'react'
import Button from './Button' 



const Card = ({name, email, id}) => {
   return (
			 <div className = 'tc w-20 bg-light-green dib pa3 ma2 br3'>
				<img alt = 'robots' src = {`https://robohash.org/${id}?200*200`}/>
					<div className = 'tc'>
						<h2>{name}</h2>
						<h2>{email}</h2>
						<Button/>
					</div>

			</div>


  );
}

export default Card

























