import React from 'react';
import Speech from 'react-speech'


const textVoicePlayer = () => <Speech 
							  play = {true}
							  text = {`My name is , and my email is I am a very efficient robot, what task can i help you with?`}
				 			  />


const Button = ({textVoicePlayer}) => {
	return (
			<div>
				<button className = 'ma3 tl f6 link dim br3 ba bw1 ph3 pv2 mb2 dib black'
				onClick = {textVoicePlayer}>
				Start talking
				</button>
				<button className = 'ma 3 tr f6 link dim br3 ba bw1 ph3 pv2 mb2 dib black'>Stop talking</button>
			</div>

  );
}

export default Button;





